<template>
  <div :class="$style.login">
    <van-field v-model="value" placeholder="输入Token" label="Token" required @click-icon="value = ''" icon="clear"/>
    <van-button type="primary" size="large" @click="click">登陆</van-button>
  </div>
</template>

<script>
import { Field, Button } from 'vant'
import { mapActions } from 'vuex'
import { CODE } from '../../config'
export default {
  name: 'x-login',
  components: {
    [Field.name]: Field,
    [Button.name]: Button
  },
  data () {
    return {
      value: ''
    }
  },
  methods: {
    ...mapActions(['logIn']),
    async click (e) {
      if (this.value) {
        const res = await this.logIn(this.value)
        if (res.code === CODE.SUCCESS) {
          this.$router.go(-1)
        } else {
          this.value = ''
          console.log(res)
          this.$toast.fail(res.msg)
        }
      }
    }
  }
}
</script>

<style module>
.login{
  width: 100%;
  height: calc(100vh - 100px);
  box-sizing: border-box;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
</style>



