<template>
  <div :class="$style.login">
    <x-header/>
    <x-login/>
  </div>
</template>

<script>
import Header from './Header'
import Login from './Login'
export default {
  name: 'page-login',
  components: {
    [Header.name]: Header,
    [Login.name]: Login
  }
}
</script>

<style module>
.login{
  width: 100%;
  min-height: 100vh;
  background-color: #ffffff;
}
</style>



